import { APIGatewayProxyEvent, APIGatewayProxyResult } from 'aws-lambda';

interface CookieOptions {
    httpOnly?: boolean;
    secure?: boolean;
    sameSite?: 'strict' | 'lax' | 'none';
    maxAge?: number;
    domain?: string;
    path?: string;
}

export const handler = async (event: APIGatewayProxyEvent): Promise<APIGatewayProxyResult> => {
    const origin = event.headers.origin || event.headers.Origin;
    const allowedOrigins = (process.env.ALLOWED_ORIGIN || '').split(',');
    const allowedOrigin = origin && allowedOrigins.includes(origin) ? origin : allowedOrigins[0];

    // Handle verify request (GET)
    if (event.resource === '/auth/session/verify') {
        console.log('Headers:', JSON.stringify(event.headers, null, 2));
        console.log('Cookie header:', event.headers.Cookie || event.headers.cookie);
        console.log('All header keys:', Object.keys(event.headers));
        
        const cookies = event.headers.Cookie || event.headers.cookie;
        console.log('Raw cookies:', cookies);

        if (cookies) {
            const parsedCookies = parseCookies(cookies);
            console.log('Parsed cookies:', parsedCookies);
        }

        return {
            statusCode: 200,
            headers: {
                'Access-Control-Allow-Origin': allowedOrigin,
                'Access-Control-Allow-Credentials': 'true'
            },
            body: JSON.stringify({
                cookiesPresent: !!cookies,
                cookies: cookies ? parseCookies(cookies) : null,
                headers: event.headers,
                debug: {
                    headerKeys: Object.keys(event.headers),
                    cookieHeader: event.headers.Cookie || event.headers.cookie,
                    allHeaders: event.headers
                }
            })
        };
    }

    // Handle POST request for setting session
    if (event.httpMethod === 'POST') {
        try {
            if (!event.body) {
                return {
                    statusCode: 400,
                    headers: {
                        'Access-Control-Allow-Origin': allowedOrigin,
                        'Access-Control-Allow-Credentials': 'true'
                    },
                    body: JSON.stringify({ message: 'Missing request body' })
                };
            }

            const { accessToken, idToken } = JSON.parse(event.body);
            
            // Set cookies with minimal restrictions for testing
            const cookieSettings = 'HttpOnly; Secure; Domain=.asaracing.live; Path=/';
            
            // Set each cookie separately
            return {
                statusCode: 200,
                headers: {
                    'Access-Control-Allow-Origin': allowedOrigin,
                    'Access-Control-Allow-Credentials': 'true',
                    'Set-Cookie': [
                        `accessToken=${accessToken}; HttpOnly; Secure; Domain=0wq09bpeu5.execute-api.us-west-1.amazonaws.com; Path=/prod; SameSite=None`,
                        `idToken=${idToken}; HttpOnly; Secure; Domain=0wq09bpeu5.execute-api.us-west-1.amazonaws.com; Path=/prod; SameSite=None`
                    ].join('; ')
                },
                body: JSON.stringify({
                    success: true
                })
            };
        } catch (error) {
            console.error('Error:', error);
            return {
                statusCode: 500,
                headers: {
                    'Access-Control-Allow-Origin': allowedOrigin,
                    'Access-Control-Allow-Credentials': 'true'
                },
                body: JSON.stringify({ message: 'Internal server error' })
            };
        }
    }

    // Handle session creation (POST)
    try {
        if (!event.body) {
            return {
                statusCode: 400,
                headers: {
                    'Access-Control-Allow-Origin': allowedOrigin,
                    'Access-Control-Allow-Credentials': 'true'
                },
                body: JSON.stringify({ message: 'Missing request body' })
            };
        }

        const { accessToken, idToken } = JSON.parse(event.body);

        if (!accessToken || !idToken) {
            return {
                statusCode: 400,
                headers: {
                    'Access-Control-Allow-Origin': allowedOrigin,
                    'Access-Control-Allow-Credentials': 'true'
                },
                body: JSON.stringify({ message: 'Missing required tokens' })
            };
        }

        const cookieOptions = {
            httpOnly: true,
            secure: true,
            sameSite: 'strict',
            maxAge: 24 * 60 * 60 * 1000
        };

        const cookies = [
            `accessToken=${accessToken}; ${serializeCookieOptions(cookieOptions)}`,
            `idToken=${idToken}; ${serializeCookieOptions(cookieOptions)}`
        ];

        return {
            statusCode: 200,
            headers: {
                'Set-Cookie': cookies.join('; '),
                'Access-Control-Allow-Origin': allowedOrigin,
                'Access-Control-Allow-Credentials': 'true'
            },
            body: JSON.stringify({ success: true })
        };
    } catch (error) {
        return {
            statusCode: 500,
            headers: {
                'Access-Control-Allow-Origin': allowedOrigin,
                'Access-Control-Allow-Credentials': 'true'
            },
            body: JSON.stringify({ message: 'Internal server error' })
        };
    }
};

function parseCookies(cookieString: string) {
    return cookieString.split(';')
        .map(cookie => cookie.trim())
        .reduce((acc, cookie) => {
            const [key, value] = cookie.split('=');
            acc[key] = value;
            return acc;
        }, {} as Record<string, string>);
}

function serializeCookieOptions(options: CookieOptions): string {
    const parts = [];
    
    if (options.httpOnly) parts.push('HttpOnly');
    if (options.secure) parts.push('Secure');
    if (options.sameSite) parts.push(`SameSite=${options.sameSite}`);
    if (options.maxAge) parts.push(`Max-Age=${Math.floor(options.maxAge / 1000)}`);
    if (options.domain) parts.push(`Domain=${options.domain}`);
    if (options.path) parts.push(`Path=${options.path}`);
    
    return parts.join('; ');
} 